const connector = async (link, fields, contents) => {
    if(!link || typeof link != "string"){
        console.log("[APP -> CONNECTOR] NO LINK PROVIDED.");
        return -1;
    }else{
        if(!fields || !contents){
            console.log("[APP -> CONNECTOR] NO DATA PROVIDED.");
            return -1;
        }else{
            if(typeof fields != "object" || typeof contents != "object"){
                console.log("[APP -> CONNECTOR] DATATYPE IS INCORRECT.");
                return -1;
            }else{
                if(fields.length != contents.length){
                    console.log("[APP -> CONNECTOR] DATA IS INCONSISTENT.");
                    return -1;
                }else{
                    const body = new FormData();
                    for(var i = 0; i < fields.length; i++){
                        body.append(fields[i], contents[i]);
                    }
                    const headers = new Headers();
                    headers.append('Access-Control-Allow-Origin', "https://jakkunught.github.io/");
                    try{
                        const res = await fetch(link, {
                            method: 'POST',
                            mode: 'no-cors',
                            redirect: 'follow',
                            headers,
                            body
                        });
                        const data = JSON.parse(await res.json());
                        return data;
                    }catch(error){
                        console.log("[APP -> CONNECTOR] FATAL ERROR.");
                        console.log(error);
                        return -1;
                    }
                }
            }
        }
    }
};

export default connector;